    */
    background-size: cover;
    margin: 0 auto;
    opacity: 1;
    -webkit-transition: opacity 1s;
    -o-transition: opacity 1s;
    transition: opacity 1s;
}
    .index .ant-layout-footer{
        margin-top: 2em;
        outline: 1px red solid;
    }
    .index .index_main{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: justify;
            justify-content: space-between;
    }
    .index .ant-layout{
        background: #fff;
    }
    .index header{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: justify;
            justify-content: space-between;
        -ms-flex-align: center;
            align-items: center;
        background: #fff;
        width: 100%;
        /*
        padding: 10px 10px;
        outline: red 1px solid;
        */
        border-bottom: 1px solid rgb(221, 221, 221);
        padding: 10px 0px;
        margin-bottom: 20px
    }
    .index header>span:nth-child(1){
    /*
    line-height: 4em;
            height: 4em;
    */
    font-size: 1.5em;
    font-weight: bold;
}
    .index .ant-input{
        /*width: 90%;*/
        
    }
    .index main:nth-of-type(1){
        width: 100%;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
        /*
        padding: 10px 10px;
        */
        background: #fff;
        /*
        outline: red 1px solid;
        */
        border-radius: 5px;
        /*
        border: 1px solid #DDD;
        box-shadow: 0 2px 0 rgba(225,225,225,.2);
        */
        padding-right: .5em;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
       
    }
    .index main:nth-of-type(2){
        width: 100%;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
        /*
        padding: 10px 10px;
        */
        background: #fff;
        /*
        outline: red 1px solid;
        */
        
        /*
        border: 1px solid #DDD;
        box-shadow: 0 2px 0 rgba(225,225,225,.2);
        */
        padding-left: .5em;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
       
    }
    .index .ant-dropdown-link{
        color: rgb(80, 86, 89);
        text-decoration: none;
        line-height: 4em;
        height: 4em;
    }
.index>p:nth-child(1){
    font-size: 20px;
    font-weight: bold;
    z-index: 1;
}
.index .indexlogin{
        // margin-right: 3em;
        z-index: 1;
}
.index .indexsignup{
        margin-left: 3em;
        z-index: 1;
}
.index .ant-spin-spinning{
        zoom: 5;
        margin: 1vh 0;
        z-index: 1;
}
.index .time{
        width: 100%;
        padding: 15px 10px;
        border: 1px solid #DDD;
        border-radius: 5px;
        -webkit-box-shadow: 0 2px 0 rgba(225,225,225,.2);
                box-shadow: 0 2px 0 rgba(225,225,225,.2);
}
.index .time .ant-btn-primary{
            width: 100%;
            color: #666;
            background-color: #f4f4f4;
            border-color: #bbb;
            -webkit-box-shadow: none;
                    box-shadow: none;
            text-shadow: none;
        
        }
/*
        margin-top: 10vh;
        width: 10vh;
        */
.index .time .ant-btn-primary span{
                font-size: 15px;
                font-weight: bold;
            }
.index .time .clock-circle >p{
                /*
                
                margin: 0;
                border-top: 1px solid #ddd;
                padding: 5px 0;
                
                float: left;
                width: 100%;
                */
            }
.index .time span>.Tomato:nth-child(2){
            margin-top: 10px;
        }
.index .time .Tomato:nth-of-type(1){
            margin-top: 10px;
        }
.index .time .Tomato:nth-of-type(1) >p{
                border-top: 1px solid #ddd;
                padding: 5px 0;
                margin: 10px 0 0 0;
                // border-bottom: 1px solid #ddd;
                word-break: break-all;
                vertical-align: middle;
                text-align: justify;
            }
.index .time .Tomato >p{
                border-top: 1px solid #ddd;
                padding: 5px 0;
                margin: 0px 0 0 0;
                word-break: break-all;
                vertical-align: middle;
                text-align: justify;
            }
.index .time .Tomato:nth-last-of-type(1) p{
                border-top: 1px solid #ddd;
                padding: 5px 0;
                margin: 0px 0 0 0;
                border-bottom: 1px solid #ddd;
                word-break: break-all;
                vertical-align: middle;
                text-align: justify;
            }
.index .time .TimeInput{
            position: relative;
        }
.index .time .TimeInput .TimeActioncancel{
                text-align: center;
                position: absolute;
                color: #9C9C97;
                font-family: Tahoma,sans;
                right: -.5em;
                cursor: pointer;
                line-height: 1rem;
                top: 0;
                margin-top: -.7em;
                background: #fff;
                border-radius: 2em;
                width: 1.2rem;
                height: 1.2rem;
                border: 1px solid #ccc;
                font-size: 1rem;
            }
/*
    todo
    */
.index .todo{
        width: 100%;
        padding: 15px 10px;
        border: 1px solid #DDD;
        border-radius: 5px;
        -webkit-box-shadow: 0 2px 0 rgba(225,225,225,.2);
                box-shadow: 0 2px 0 rgba(225,225,225,.2);
}
.index .todo .ant-checkbox-group{
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: column;
                flex-direction: column;
        }
.index .todo .todo-list{
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: column;
                flex-direction: column;
        }
.index .todo .todo-list .ant-checkbox-wrapper:nth-of-type(1){
                margin-left: 8px;
            }
.index .todo .todo-list .ant-checkbox-wrapper{
                word-break: break-all;
                vertical-align: middle;
                text-align: justify;
            }
.index .todo .todo-list >span{
                display: -ms-flexbox;
                display: flex;
                border-top: 1px solid #ddd;
                padding: 5px 0;
            }
.index .todo .todo-list >span:nth-of-type(1){
                margin-top: 10px;
            }
.index .todo .todo-list >span>span{
                -ms-flex: 1 1;
                    flex: 1 1;
                // width: 100%;
            }
.index .todo .complate{
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: column;
                flex-direction: column;
            /*
            .ant-collapse > .ant-collapse-item > .ant-collapse-header{
                padding: 0;
            }
            .ant-btn{
                width: 50%;
            }
            */
        }
.index .todo .complate .ant-checkbox-wrapper:nth-of-type(1){
                margin-left: 8px;
            }
.index .todo .complate .ant-collapse-content-box{
            display: -ms-flexbox;
            display: flex;
            -ms-flex-direction: column;
                flex-direction: column;
            }
.index .todo .complate .ant-collapse-content-box >span{
                    display: -ms-flexbox;
                    display: flex;
                    border-top: 1px solid #ddd;
                    padding: 5px 0;
                }
.index .todo .complate .ant-collapse-content-box >span:nth-of-type(1){
                    margin-top: 10px;
                }
.index .todo .complate .ant-collapse-content-box >span>span{
                    -ms-flex: 1 1;
                        flex: 1 1;
                    // width: 100%;
                }
.index .todo .complate .ant-checkbox-wrapper-checked{
                word-break: break-all;
                vertical-align: middle;
                text-align: justify;
                opacity: .5;
                text-decoration: line-through;
            }
.index .todo .complate .CleanIcon{
                float: right;
                padding-top: 20px;
                cursor: pointer;
            }
.index .todo .complate .ant-collapse-borderless{
                width: 95%;
            }
.index .todo .complate .noselect {/*文本不能选中*/
                -webkit-touch-callout: none;
                -webkit-user-select: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;
            }
.index .check-circle{
        width: 100%;
        text-align: center;
        margin-top: 20px;
        border-top: 1px solid rgb(221, 221, 221);
        /*
        padding-top: 20px;
        */
        padding: 2em;
}
.index .check-circle p{
            color: #999;
            margin: 1em;
        }
.index .check-circle .anticon svg{
            width: 80px;
            height: 80px;
            fill: #DDD;
        }
.index .clock-circle{
        width: 100%;
        text-align: center;
        margin-top: 20px;
        border-top: 1px solid rgb(221, 221, 221);
        /*
        padding-top: 20px;
        */
        padding: 2em;
}
.index .clock-circle p{
            color: #999;
            margin: 1em;
        }
.index .clock-circle .anticon svg{
            width: 80px;
            height: 80px;
            fill: #DDD;
        }
.index .kv-vbg{
        position: absolute;
        width: 100%;
        height: 100%;
        -o-object-fit: fill;
           object-fit: fill;
}
@media (max-width: 768px) {
    .index .index_main{
            -ms-flex-direction: column;
                flex-direction: column;
    }
    .index main:nth-of-type(2){
            padding: 0;
            margin-top: 20px;
            margin-bottom: 20px;
    }
    .index main:nth-of-type(1){
            padding: 0;
    }
}
.opsity{
    opacity: 0;
}


